/*.t4.my-inside article {  commented out by MH
    display: flex;
}*/

.t4.my-inside .bg-image-full {
    min-height: 100px;
    padding: 1em 7%;
    /*background-image: linear-gradient(-180deg, rgba(0,132,83,1) 5%, rgba(0,132,83,1) 50%, rgba(0,78,56,1) 100%);*/
}

.t4.my-inside #quadrants {
    align-self: center;
}

.t4.my-inside aside.sidebar {
    display: none;
}

.t4.my-inside article .box1 {
    background: #fff;
}

.t4.my-inside article .box3.general,
.t4.my-inside article .box4.calendar {
    background: #f0e1b0;
}

.t4.my-inside article .box4.general {
    background: #fff;
}

.t4.my-inside article .box3.table {
    background: #aa985e;
}

.t4.my-inside article .box1 p,
.t4.my-inside article .box1 li,
.t4.my-inside article .box4 p,
.t4.my-inside article .box4 li {
    color: #000;
}

.t4.my-inside article .box1 h2,
.t4.my-inside article .box1 h3,
.t4.my-inside article .box4 h2,
.t4.my-inside article .box4 h3 {
    color: #043927;
}

.t4.my-inside article .box1 a.button-cta,
.t5.my-inside article a.button-cta,
.t5.my-inside article a.button-cta:link,
.t5.my-inside article a.button-cta:visited {
    color: #043927;
    border: 2px solid #043927;
    background: rgba(196, 182, 129, .15);
    font-weight: 500;
}

.t4.my-inside a.button-cta,
.t4.my-inside a.button-default {
    color: #00573d;
    text-decoration: none;
}

.t4.my-inside a:not(.button-cta),
.t4.my-inside a:not(.button-default),
.t4.my-inside a.button-cta:hover,
.t4.my-inside a.button-default:hover {
    color: #043927;
    text-decoration: underline;
}


.t4.my-inside article .box1 a.button-cta:hover,
.t5.my-inside article a.button-cta:hover {
    color: #00573d;
    border: 2px solid #00573d;
    background: #fff;
    text-shadow: none;
    box-shadow: rgba(196, 182, 129, .6) 0 1px 3px;
    text-decoration: underline;
}

.t4.my-inside article .button {
    background: #c4b581;
}

.t4.my-inside article .box4.button {
    background: #dad490;
}

.t4.my-inside article .button .side-nav h2 {
    background: #fff;
}

.t4.my-inside article .button .side-nav h3 {
    background: #ece7d4;
}

.t4.my-inside article .button .side-nav ul:last-of-type {
    border-bottom: 2px solid #f0e1b0;
}

.t4.my-inside article .button .side-nav li {
    border-bottom: 1px solid #f0e1b0;
    background: #ece7d4;
}

.t4.my-inside article .button .side-nav li a {
    background: rgba(255, 255, 255, 0.5);
    color: #333;
}

.t4.my-inside article .general a {
    color: #043927;
}

.t4.my-inside article .general a:hover {
    color: #021e14;
    text-decoration: none;
}

.t4.my-inside article .button .side-nav li a:hover {
    color: #043927;
    background: rgba(207, 182, 104, 0.15);
}

@media screen and (min-width: 1140px) {
    .t4.my-inside article {
        background-image: linear-gradient(180deg, rgba(225, 197, 122, 1) 0, rgba(240, 225, 176, 1) 3%, rgba(255, 255, 255, 1) 60%, rgba(255, 255, 255, 1) 100%);
        padding: 0;
    }
}

.my-inside section {
    margin-top: 0;
    padding: 2em 0;
}

.t5.my-inside section ul:not(.ul-button-stack),
.t5.my-inside section ul:not(.ul-button-2wide),
.t5.my-inside section ol:not(.ul-button-stack),
.t5.my-inside section ol:not(.ul-button-2wide) {
    margin-top: 0;
}

.t4.my-inside article ul.ul-button-stack,
.t4.my-inside article ul.ul-button-2wide,
.t4.my-inside article ol.ul-button-stack,
.t4.my-inside article ol.ul-button-2wide,
.t5.my-inside section ul.ul-button-stack,
.t5.my-inside section ul.ul-button-2wide,
.t5.my-inside section ol.ul-button-stack,
.t5.my-inside section ol.ul-button-2wide {
    margin-top: 0;
    list-style: none;
}

.t5.my-inside.two-column section ul.ul-button-stack,
.t5.my-inside.two-column section ol.ul-button-stack {
    margin-top: 0;
    list-style: none;
}

.t5.my-inside section .side-nav ul,
.t5.my-inside section .side-nav ol {
    padding-left: 0;
    list-style: none;
}

.t5.my-inside .bg-image-full {
    min-height: 100px;
    padding: 1em 7%;
    /*background-image: linear-gradient(320deg, rgba(0,132,83,1) 5%, rgba(0,132,83,1) 50%, rgba(0,78,56,1) 100%);*/
}

.t5.my-inside article {
    background: #f7f4e9 url(../images/background-accent-double-ss.png) no-repeat right bottom;
    background-size: contain;
}

.t5.my-inside .one-column section h1 + p,
.t5.my-inside .two-column section h1 + p {
    font-size: 16px;
}

@media screen and (min-width: 768px) {
    .t5.two-column.showcase.my-inside section div.row div.col-md-6:nth-child(2) {
        /*margin-top: 5em;*/
    }
}



/*V2 092920*/

.t4.my-inside.page-inside-irt .jumbotron:not(.flex-section) {
    background-image: linear-gradient(60deg, #c4b581 20%, #dad490 100%);
    background-blend-mode: unset;
}

.t4.my-inside.page-inside-irt #spotlight.jumbotron h2 {
    color: #333;
}

.t4.my-inside.page-inside-irt .jumbotron #spotlight-boxes div.socialbox {
    box-shadow: rgba(196, 181, 129, .7) 0 3px 3px;
    border-right: 1px solid #c4b581;
    border-bottom: 1px solid #c4b581;
    border-radius: 8px;
    overflow: hidden;
}

.t4.my-inside #spotlight div.socialbox a {
    background: url(../images/comb-pattern-1.svg),
    linear-gradient(-20deg, rgb(255, 255, 255) 0%, rgb(255, 255, 255) 35%, #f0e1b0 100%);
    background-blend-mode: multiply;
}

/* line 65 */

.t4.my-inside.page-inside-irt article .box2.button {
    background: #dad490;
}

.t4.my-inside.page-inside-irt article .box4.button {
    background: #c4b581;
}

